import { Component, Input, OnInit, SimpleChanges } from '@angular/core';

@Component({
  selector: 'lib-titlesecondary',
  templateUrl: './titlesecondary.component.html',
  styleUrls: ['./titlesecondary.component.sass'],
})
export class TitlesecondaryComponent implements OnInit {
  @Input() infoAdicional: Array<any> = [];
  title: string;
  subtitle: string;

  constructor() {
    this.title = '';
    this.subtitle = '';
  }

  ngOnChanges(changes: SimpleChanges) {
    const { infoAdicional } = changes;
    if (infoAdicional?.currentValue !== infoAdicional?.previousValue) {
      this.title = this.infoAdicional[0]?.title;
      this.subtitle = this.infoAdicional[0]?.subtitle;
    }
  }

  ngOnInit() {
    this.title = this.infoAdicional[0]?.title;
    this.subtitle = this.infoAdicional[0]?.subtitle;
  }
}
