import { CurrencyPipe, DatePipe } from '@angular/common';
import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Output,
  QueryList,
  SimpleChanges,
  ViewChild,
  ViewChildren,
} from '@angular/core';

import * as jsPDF from 'jspdf';
import 'jspdf-autotable';

import { Angular2Txt } from 'angular2-txt/Angular2-txt';
import { ExcelService } from '../services/excel.service';
import { TableComponent } from '../component/table/table.component';

enum NumberFormat {
  NUM = 'number',
  CUR = 'currency',
}

enum AlignClass {
  LEFT = 'text-align: start;',
  RIGHT = 'text-align: end;',
}

enum FormatType {
  NUM = 'number',
  CUR = 'currency',
  DIF = 'difference',
  COU = 'count',
}

interface IReportConfig {
  title: string;
  subtitle: string;
  subject: string;
  sessionDate: Date;
  tableTitle: string;
  customHeader?: string[];
  customIndexRows?: number[];
  customTotalRows?: number[];
  customTotalFormatting?: NumberFormat[];
  customTotalType?: FormatType[];
  footLabel?: string;
}

interface IFooterConfig {
  customTotalRows?: number[];
  customTotalFormatting?: NumberFormat[];
  customTotalType?: FormatType[];
  customTotalAlign?: AlignClass[];
}

interface ISearchParamsProps {
  bancorresponsal: string;
  desde?: string;
  hasta?: string;
  ruc: string;
  tipo: string;
  numfactura?: string;
}

@Component({
  selector: 'nexti-lib-datatable',
  templateUrl: './nexti-lib-datatable.component.html',
  styleUrls: ['./nexti-lib-datatable.component.sass'],
})
export class NextiLibDatatableComponent implements OnInit {
  @Input() header: Array<any>;
  @Input() customHeader?: boolean;
  @Input() list: Array<any>;
  @Input() titleEmpty: string;
  @Input() title: string;
  @Input() titleXs: boolean = false;
  @Input() btnModal: boolean;
  @Input() recursos: any;
  @Input() selectFilter: any;
  @Input() hiddenActions: boolean;
  @Input() breadcrumbs: Array<any>;
  @Input() infoAdicional: Array<any>;
  @Input() linkBack: boolean = false;
  @Input() reportConfig?: IReportConfig;
  @Input() opOcultarBuscar: boolean;
  @Input() opOcultarFooter: boolean;
  @Input() footerConfig?: IFooterConfig;
  @Input() actionState: boolean = false;
  @Input() paginacion?: boolean = false;
  @Input() totalRegistros?: number = 0;
  @Input() multFPP?: number = 0;
  @Input() actualPage?: number = 0;

  @Input() excelData?: any;
  @Input() multiTable: boolean = false;
  @Input() sections: boolean = false;
  @Input() isLoading?: boolean;

  @Output() onDelete = new EventEmitter<any>();
  @Output() onUpdate = new EventEmitter<any>();
  @Output() onActive = new EventEmitter<any>();
  @Output() onShow = new EventEmitter<any>();
  @Output() onShowEvent = new EventEmitter<any>();
  @Output() onBack = new EventEmitter<any>();
  @Output() onfilter = new EventEmitter<any>();
  @Output() onNext = new EventEmitter<any>();
  @Output() onReturn = new EventEmitter<any>();
  @ViewChild('search') inputSearch!: ElementRef;
  @ViewChildren(TableComponent) datatable: QueryList<TableComponent>;

  data$ = new Array<any>();
  // TODO mejorar, con un estado de header normalizado se puede prescindir de esta variable
  dataPerm$ = new Array<any>();
  originalList: Array<any>;
  footerRow = [];
  filterSelected: any = null;

  constructor(
    private datePipe: DatePipe,
    private currencyPipe: CurrencyPipe,
    private excelService: ExcelService
  ) {}

  ngOnInit() {
    this.selectFilter?.forEach((element) => {
      if (element.selected) {
        this.filterSelected = element.value;
      }
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { list } = changes;
    if (list?.currentValue !== list?.previousValue) {
      this.data$ = list?.currentValue;
      this.dataPerm$ = list?.currentValue;
      if (this.inputSearch) this.inputSearch.nativeElement.value = '';
    }
  }

  ngDoCheck() {
    this.datatable?.forEach((element) => {
      if (this.footerConfig) element.getFooterData();
    });
  }

  onFilter() {
    this.onfilter.emit(this.filterSelected);
  }

  onDeleteItem(item: any) {
    this.onDelete.emit(item);
  }

  onUpdateItem(item: any) {
    this.onUpdate.emit(item);
  }

  onActiveItem(item: any) {
    this.onActive.emit(item);
  }

  onShowItem(item: any) {
    this.onShow.emit(item);
  }

  onShowItemEvent($event: any) {
    this.onShowEvent.emit($event);
  }

  onlyNumbers(event) {
    return event.charCode >= 48 && event.charCode <= 57;
  }

  customSearchBancoFn(term: string, item: any) {
    term = term.toLowerCase();

    return item.bancorresponsal.toLowerCase().includes(term);
  }

  customSearchCompaniesFn(term: string, item: any) {
    term = term.toLowerCase();

    return (
      item.ruc.includes(term) || item.razonSocial.toLowerCase().includes(term)
    );
  }

  filter(search: string) {
    if (this.multiTable) {
      let datafilter = [];
      for (const list of this.dataPerm$) {
        let data = this.filterList(list, search);
        datafilter.push(data);
      }
      this.data$ = datafilter;
    } else {
      this.data$ = this.filterList(this.dataPerm$, search);
    }
  }

  filterList(list, search) {
    return list.filter((lista) => {
      for (const row of lista) {
        if (typeof row === 'object') {
          if (
            (row.title && this.toCompareString(row.title, search)) ||
            (row.subtitle && this.toCompareString(row.subtitle, search))
          ) {
            return lista;
          }
        } else if (this.toCompareString(row, search)) {
          return lista;
        }
      }
    });
  }

  toCompareString(item, search) {
    return item
      .toString()
      .toUpperCase()
      .includes(search.toString().toUpperCase());
  }

  getReportHeader(): string[] {
    const { customHeader } = this.reportConfig;

    return customHeader
      ? customHeader
      : this.header.map((item) => {
          if (typeof item === 'object') {
            return item.title;
          } else {
            return item;
          }
        });
  }

  getFormatDataToTreatment(item) {
    if (typeof item === 'object') {
      if (item != null) {
        if (item.title && item.subtitle) {
          return `${item.title}\n${item.subtitle}`;
        } else {
          return item.title ? item.title : item.subtitle && item.subtitle;
        }
      } else {
        return '';
      }
    } else {
      return item;
    }
  }

  getRawData(customTotalRows, formatedData, customTotalType, rawDataRow) {
    if (customTotalRows && customTotalRows.length) {
      customTotalRows.forEach((customIndex, indexCol) => {
        formatedData.forEach((item, index) => {
          if (customIndex === index) {
            let returnItem = 0;

            customTotalType.forEach((type, indexType) => {
              if (indexCol === indexType) {
                switch (type) {
                  case FormatType.NUM:
                    if (
                      (typeof item === 'string' &&
                        (item == '--' || item === '')) ||
                      typeof item === 'undefined'
                    ) {
                      returnItem = 0;
                    } else {
                      returnItem = Number(item);
                    }
                    break;
                  case FormatType.CUR:
                    if (
                      (typeof item === 'string' &&
                        (item == '--' || item === '')) ||
                      typeof item === 'undefined'
                    ) {
                      returnItem = 0;
                    } else {
                      returnItem = this.formatCurrencyToNumber(item);
                    }
                    break;
                  case FormatType.COU:
                    if (
                      (typeof item === 'string' &&
                        (item == '--' || item === '')) ||
                      typeof item === 'undefined'
                    ) {
                      returnItem = 0;
                    } else {
                      returnItem = 1;
                    }
                    break;
                  default:
                    returnItem = 0;
                }
              }
            });
            rawDataRow.push(returnItem);
          }
        });
      });
    }
  }

  getReportBody() {
    const {
      customIndexRows,
      customTotalRows,
      customTotalFormatting,
      customTotalType,
      footLabel,
    } = this.reportConfig;

    let tableData = [];
    let tableFooterData = [];
    let rawData = [];

    this.data$.forEach((itemArray) => {
      let tableDataRow = [];
      let rawDataRow = [];

      // Step 1: format - prepare dat
      const formatedData = itemArray.map((item) =>
        this.getFormatDataToTreatment(item)
      );

      // Step 2: Generate Filter data
      if (customIndexRows && customIndexRows.length) {
        customIndexRows.forEach((customIndex) => {
          formatedData.forEach((item, index) => {
            if (customIndex === index) {
              tableDataRow.push(item);
            }
          });
        });
      } else {
        tableDataRow = formatedData;
      }

      // Step 3: Get raw data (values to totals)
      this.getRawData(
        customTotalRows,
        formatedData,
        customTotalType,
        rawDataRow
      );

      tableData.push(tableDataRow);
      rawData.push(rawDataRow);
    });

    if (customTotalRows && customTotalRows.length && rawData.length) {
      let procesedTotals = [];

      procesedTotals = this.trasposeMatrix(rawData).map((itemArray) =>
        itemArray.reduce((a, b) => a + b, 0)
      );

      if (customTotalFormatting && customTotalFormatting.length) {
        procesedTotals = customTotalFormatting.map((format, index) => {
          switch (format) {
            case NumberFormat.CUR:
              return this.currencyPipe.transform(procesedTotals[index]);
            default:
              return procesedTotals[index];
          }
        });
      }

      procesedTotals = [[footLabel, ...procesedTotals]];

      tableFooterData = procesedTotals.length ? procesedTotals : null;
    }

    return {
      tableData,
      tableFooterData,
    };
  }

  generatePdf() {
    const pdf = new jsPDF.jsPDF();
    pdf.setProperties({
      title: 'Resultados Netos',
    });

    const { title, subtitle, subject, sessionDate, tableTitle } =
      this.reportConfig;

    const pageSize = pdf.internal.pageSize;
    const pageWidth = pageSize.width ? pageSize.width : pageSize.getWidth();

    // Section I
    (pdf as any).autoTable({
      body: [
        [title, this.datePipe.transform(Date.now(), 'dd/MM/yyyy HH:mm')],
        [
          {
            content: subtitle,
            colSpan: 2,
            styles: { fontSize: 12, fontStyle: 'normal' },
          },
        ],
      ],
      showHead: false,
      theme: 'plain',
      styles: {
        fillColor: [48, 84, 150],
        textColor: 255,
        valign: 'bottom',
      },
      columnStyles: {
        0: { fontStyle: 'bold', fontSize: 13 },
        1: { fontSize: 11, halign: 'right' },
      },
    });

    // Section II left
    const lastTableY = (pdf as any).lastAutoTable.finalY + 5;

    (pdf as any).autoTable({
      body: [[subject]],
      showHead: false,
      theme: 'plain',
      startY: lastTableY,
      styles: {
        fillColor: [255, 255, 153],
        textColor: 0,
        valign: 'bottom',
        fontSize: 11,
        fontStyle: 'bold',
        overflow: 'hidden',
      },
      margin: { right: 100 },
    });

    // Section II right
    (pdf as any).autoTable({
      body: [
        [
          'SESION COMPENSACION:',
          this.datePipe.transform(sessionDate, 'dd/MM/yy, HH:mm', 'UTC'),
        ],
      ],
      showHead: false,
      theme: 'plain',
      startY: lastTableY,
      styles: {
        fillColor: [255, 255, 153],
        textColor: 0,
        valign: 'bottom',
        fontSize: 10,
        overflow: 'hidden',
      },
      margin: { left: pageWidth - 90 },
      columnStyles: {
        1: { halign: 'right' },
      },
    });

    // Section III
    (pdf as any).autoTable({
      body: [[tableTitle]],
      showHead: false,
      theme: 'plain',
      startY: (pdf as any).lastAutoTable.finalY + 10,
      styles: {
        fillColor: [31, 78, 120],
        textColor: 255,
        valign: 'bottom',
        fontSize: 11,
        fontStyle: 'bold',
      },
    });

    const header = [this.getReportHeader()];
    const { tableData, tableFooterData } = this.getReportBody();

    // Section IV
    const tableRenderObj: any = {
      head: header,
      body: tableData,
      theme: 'striped',
      showHead: 'firstPage',
      showFoot: 'lastPage',
      startY: (pdf as any).lastAutoTable.finalY,
      headStyles: { fillColor: [214, 220, 228], textColor: 0 },
      footStyles: { fillColor: [31, 78, 120], textColor: 255 },
      foot: tableFooterData.length ? tableFooterData : false,
      styles: { fontSize: 9 },
    };

    (pdf as any).autoTable(tableRenderObj);

    // Open PDF document in browser's new tab
    // pdf.output('pdfobjectnewwindow');
    pdf.save();
  }

  generateTxt() {
    const header = this.getReportHeader();

    const options = {
      headers: header,
      fieldSeparator: ';',
      quoteStrings: '',
      decimalseparator: ',',
    };

    let { tableData, tableFooterData } = this.getReportBody();

    tableData = [header, ...tableData];
    tableData = tableFooterData.length
      ? [...tableData, ...tableFooterData]
      : tableData;

    const data = this.convertToJSON(tableData);
    new Angular2Txt(data, 'Resultados Netos', options);
  }

  getFooterData() {
    const {
      customTotalRows,
      customTotalFormatting,
      customTotalType,
      customTotalAlign,
    } = this.footerConfig;

    let tableFooterData = [];
    let rawData = [];

    this.data$.forEach((itemArray) => {
      let rawDataRow = [];

      // Step 1: format - prepare dat
      const formatedData = itemArray.map((item) =>
        this.getFormatDataToTreatment(item)
      );

      // Step 2: Get raw data (values to totals)
      this.getRawData(
        customTotalRows,
        formatedData,
        customTotalType,
        rawDataRow
      );

      rawData.push(rawDataRow);
    });

    if (customTotalRows && customTotalRows.length && rawData.length) {
      let procesedTotals = [];

      procesedTotals = this.trasposeMatrix(rawData).map((itemArray) =>
        itemArray.reduce((a, b) => a + b, 0)
      );

      if (customTotalFormatting && customTotalFormatting.length) {
        procesedTotals = customTotalFormatting.map((format, index) => {
          let value, align;

          switch (format) {
            case NumberFormat.CUR:
              value = this.currencyPipe.transform(procesedTotals[index]);
              break;
            default:
              value = procesedTotals[index];
              break;
          }

          align = customTotalAlign[index];

          return {
            value,
            align,
          };
        });
      }

      tableFooterData = procesedTotals.length ? procesedTotals : null;
    }

    let finalFooterData = [];

    // Clear data
    if (customTotalRows && customTotalRows.length) {
      let cont = 0;
      for (let i = 0; i < this.header.length; i++) {
        let itemToPush;

        customTotalRows.forEach((customIndex) => {
          if (customIndex === i) {
            itemToPush = tableFooterData[cont];
            cont++;
          }
        });

        finalFooterData.push(itemToPush);
      }
    }

    this.footerRow = finalFooterData;
  }

  exportAsXLSX(): void {
    this.excelService.exportAsExcelFile(
      this.excelData,
      'planilla_compensacion'
    );
  }

  formatCurrencyToNumber(value: string): number {
    if (value !== undefined && value !== null)
      return Number(String(value).replace(/[^0-9.-]+/g, ''));
  }

  isNumber(value: any) {
    let regex = /^\d*\.?\d*$/;
    return regex.test(value);
  }

  trasposeMatrix(matrix) {
    return matrix[0].map((_, colIndex) => matrix.map((row) => row[colIndex]));
  }

  convertToJSON(array) {
    var objArray = [];
    for (var i = 1; i < array.length; i++) {
      objArray[i - 1] = {};
      for (var k = 0; k < array[0].length && k < array[i].length; k++) {
        var key = array[0][k];
        let actualElement = array[i][k];
        let el = actualElement;
        if (typeof actualElement === 'string') {
          el = actualElement.replace(/\n/g, ' ');
        } else if (typeof actualElement === 'undefined') {
          el = '';
        }
        objArray[i - 1][key] = el;
      }
    }

    return objArray;
  }

  onBackItem() {
    this.onBack.emit();
  }
}
